.st-wizard-wrapper {
    .st-wizard-steps {
        .wizard-step {
            display: flex;
            color: theme-color(primary);
            padding-top: 10px;
            margin-top: 25px;

            .step-number {
                font-size: 40px;
                font-weight: 600;
                margin-bottom: 0;
            }

            .step-details {
                margin-bottom: 0;
            }
        }
    }
}